<script>
export default {
    data() {
        return {
            isBtn1Active: true,
            isBtn2Active: false,
            isBtn3Active: false,
        }
    },
    methods: {
        showAnimal1Content() {
            this.isBtn1Active = true;
            this.isBtn2Active = false;
            this.isBtn3Active = false;
            this.$refs.animal1Content.style.display = 'block';
            this.$refs.animal2Content.style.display = 'none';
            this.$refs.animal3Content.style.display = 'none';
        },
        showAnimal2Content() {
            this.isBtn1Active = false;
            this.isBtn2Active = true;
            this.isBtn3Active = false;
            this.$refs.animal1Content.style.display = 'none';
            this.$refs.animal2Content.style.display = 'block';
            this.$refs.animal3Content.style.display = 'none';
        },
        showAnimal3Content() {
            this.isBtn1Active = false;
            this.isBtn2Active = false;
            this.isBtn3Active = true;
            this.$refs.animal1Content.style.display = 'none';
            this.$refs.animal2Content.style.display = 'none';
            this.$refs.animal3Content.style.display = 'block';
        },
    }
}
</script>

